import React from "react"
import { Link, graphql, StaticQuery } from "gatsby"
import { css } from "@emotion/core"
import { rhythm } from "../utils/typography"
import { ThemeToggler } from "gatsby-plugin-dark-mode"
import Toggle from "./Toggle"
import sun from "../images/sun.png"
import moon from "../images/moon.png"

const withSiteMetadata = Component => {
  return props => {
    return (
      <StaticQuery
        query={graphql`
          query LayoutQuery {
            site {
              siteMetadata {
                title
              }
            }
          }
        `}
        render={data => <Component data={data} {...props} />}
      />
    )
  }
}

const MenuLink = props => (
  <Link
    to={props.to}
    css={css`
      text-decoration: none;
      color: var(--menuLink);
      transition: opacity 0.2s ease-in-out;
      opacity: 60%;
      &:hover {
        opacity: 100%;
      }
      & + & {
        margin-top: ${rhythm(1)};
      }

      @media (min-width: 576px) {
        & + & {
          margin-top: 0;
          margin-left: ${rhythm(1)};
        }
      }
    `}
    activeStyle={{ opacity: 1 }}
  >
    {props.children}
  </Link>
)

class Layout extends React.Component {
  state = {
    theme: null,
  }

  render() {
    const { children, data } = this.props

    return (
      <div
        css={css`
          color: var(--textNormal);
          padding: ${rhythm(2)} ${rhythm(1)};
          margin: 0 auto;
          max-width: 680px;
          transition: "color 2s ease-out, background 2s ease-out, background-color 2s ease-out";
        `}
      >
        <div
          css={css`
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: ${rhythm(2)};

            @media (min-width: 576px) {
              flex-direction: row;
            }
          `}
        >
          <Link
            to="/"
            css={css`
              width: 100%;
              margin-bottom: ${rhythm(2)};
              text-align: center;

              @media (min-width: 576px) {
                width: unset;
                margin-bottom: 0;
                text-align: unset;
              }
            `}
          >
            <h2
              css={css`
                display: inline-block;
                font-style: normal;
                margin-bottom: 0;
                color: var(--websiteTitle);
              `}
            >
              {data.site.siteMetadata.title}
            </h2>
          </Link>
          <div
            css={css`
              display: flex;
              flex-direction: column;
              margin-left: 0;
              align-items: center;

              @media (min-width: 576px) {
                margin-left: auto;
                flex-direction: row;
              }
            `}
          >
            <MenuLink to="/">About Me</MenuLink>
            <MenuLink to="/blog/">Blog</MenuLink>
            <MenuLink to="/contact/">Contact Me</MenuLink>
          </div>
        </div>
        <ThemeToggler>
          {({ theme, toggleTheme }) => (
            <Toggle
              icons={{
                checked: (
                  <img
                    src={moon}
                    alt="moon"
                    width="16"
                    height="16"
                    role="presentation"
                    style={{ pointerEvents: "none" }}
                  />
                ),
                unchecked: (
                  <img
                    src={sun}
                    alt="sun"
                    width="16"
                    height="16"
                    role="presentation"
                    style={{ pointerEvents: "none" }}
                  />
                ),
              }}
              checked={theme === "dark"}
              onChange={e => toggleTheme(e.target.checked ? "dark" : "light")}
            />
          )}
        </ThemeToggler>
        <div>{children}</div>
      </div>
    )
  }
}

export default withSiteMetadata(Layout)
